form.form-compatitor-container {
    width: 80%;
    margin: 3rem auto;
    background-color: #fff;
    border-radius: 50px;
    border: 8px solid #000;
    display: grid;
    grid-template-columns: 1fr;
    overflow: hidden;
    position: relative;
}

div.form-compatitor{
    position: relative;
    background-color: #fff;
    display: grid;
    grid-template-columns: 1fr;
}


div.form-compatitor.content-input {
    border: 3px solid #000;
    padding: 1rem 2rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
}

div.form-compatitor.content-input>label {
    font-family: "Nunito Black";
    color: #003595;
    font-size: 2rem;
}

div.form-compatitor.content-input>input {
    font-family: "Nunito Black";
    color: #333;
    font-size: 1.5rem;
    border: none;
    margin-left: 1rem;
}

div.form-compatitor.content-input > textarea:focus, div.form-compatitor.content-input > input:focus{
    outline: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

#send-button{
    padding: .5rem .7rem;
    border-radius: 5vw;
    width: 15vw;
    background-color: #fff;
    border: 5px solid #000;
    font-size: 2rem;
    font-family: "Nunito Black";
    font-weight: bolder;
    margin: 1rem auto .2rem auto;
    cursor: pointer;
}

#send-button.enable{
    color: #3ec1f2;
}

#send-button.disable{
    color: #888;
    cursor: not-allowed;
}

.form-section.disclaimer{
    color: #fff;
    font-family: "OpenSans";
    font-weight: 600;
    padding: 0 10rem;
    font-size: 1.2rem;
}

.form-section.container-checkbox{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.form-section.container-checkbox > .form-section.valid_disclaimer{
    color:#fff;
    width: 2rem;
    height: 2rem;
    border-radius: 25%;
    border: 3px solid #000;
}
.form-section.container-checkbox > .form-section.text_checkbox{
    color:#fff;
    font-family: "OpenSans";
    font-weight: 600;
    margin-left: 1rem;
    font-size: 1.2rem;
}

.list-category-select-reset{
    color:#000;
    margin-right:1vw;
    cursor: pointer;
}

@media only screen and (max-width:1024px) {
    form.form-compatitor-container {
        margin: 2rem auto;
    }

    div.form-compatitor.content-input {
        width: 100%;
        border: 3px solid #000;
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;
    }

    div.form-compatitor.content-input>label {
        font-family: "Nunito Black";
        color: #003595;
        font-size: 6vw;
    }
    
    div.form-compatitor.content-input>input {
        font-family: "Nunito Black";
        color: #333;
        font-size: 5vw;
        border: none;
        margin-left: 0;
    }

    #send-button{
        padding: .5rem .7rem;
        border-radius: 5vw;
        width: auto;
        background-color: #fff;
        border: 5px solid #000;
        font-size: 1.2rem;
        font-family: "Nunito Black";
        font-weight: bolder;
        margin: 1rem auto;
        cursor: pointer;
    }

    .form-section.disclaimer{
        color: #fff;
        font-family: "OpenSans";
        font-weight: lighter;
        padding: 0 2rem;
        font-size:10px;
        text-align: justify;
    }

    .form-section.container-checkbox{
        margin-bottom: 3rem;
    }
}