div.footer{
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
}

.footer-img{
    width: 100%;
}
img.footer-img-facocaribe{
    position: absolute;
    bottom: 4.3vw;
    left: 5rem;
}
.footer-img-alcon{
    position: absolute;
    right: 5rem;
    bottom: 4.3vw;
}

.footer-img-movil{
    display: none;
}

@media only screen and (max-width:1024px) {
    img.footer-img-facocaribe{
        position: absolute;
        bottom: 4.3vw;
        left: 5vw;
        width: 25vw;
        height: auto;

    }

    .footer-img-alcon{
        position: absolute;
        right: 5vw;
        bottom: 4.3vw;
        width: 13vw;
        height: auto;
    }
}

@media only screen and (max-width:500px) {
    .footer-img-alcon, img.footer-img-facocaribe, .footer-img{
        display: none;
    }

    .footer-img-movil{
        display: block;
        width: 100%;
        height: auto;
    }
}